.page{min-height:100vh;display:flex;align-items:center;justify-content:center;padding:6rem 1.5rem;background:#fff}.container{max-width:540px;text-align:center;display:flex;flex-direction:column;gap:1.5rem}.title{font-size:clamp(1.8rem,4vw,2.6rem);font-weight:400;letter-spacing:-0.02em;color:#111827;margin:0}.description{font-size:clamp(1rem,2.3vw,1.2rem);line-height:1.7;color:#4b5563;margin:0}.backLink{align-self:center;display:inline-flex;align-items:center;gap:.5rem;font-size:.95rem;color:#2563eb;text-decoration:none;border-bottom:1px solid rgba(0,0,0,0);padding-bottom:.2rem;transition:border-color .2s ease,color .2s ease}.backLink:hover{color:#1d4ed8;border-color:rgba(37,99,235,.4)}